import React from 'react';
import YouTube from 'react-youtube';


class VideoGrid extends React.Component {

    componentDidMount() {
        window.addEventListener('orientationchange', () => {setTimeout( () => {this.setState({})}, 200)} );
    }

    render() {
        let opts = {
            width: window.innerWidth / 2.5,
            height: window.innerHeight / 2,
            playerVars: {
                color: 'white',
                autoplay: 0,
                allowfullscreen: 0
            },
        };
        if (/Android|webOS|iPhone|iPad|iPod|BlackBerry|IEMobile|Opera Mini/i.test(navigator.userAgent)) {
            if (window.matchMedia("(orientation: portrait)").matches) {
                opts = {
                    width: window.innerWidth / 2.7,
                    height: window.innerHeight / 4,
                    playerVars: {
                        color: 'white',
                        autoplay: 0,
                        allowfullscreen: 0
                    },
                };
            } else {
                opts = {
                    width: window.innerWidth / 2.3,
                    height: window.innerHeight / 1.5,
                    playerVars: {
                        color: 'white',
                        autoplay: 0,
                        allowfullscreen: 0
                    },
                };
            }
        }

        let links = [
            { text: 'Mecna', link: 'LJtOZH701ZI' },
            { text: 'ChiamamiFaro', link: 'nmRxDltsFZY' },
            { text: 'Carolina', link: 'HzTJkXgs44c' },
            { text: 'Carolina', link: 'Lex3FXEpr0Y' },
        ]
        return (
            <div id="videoGrid">
                {
                    links.map((item) =>
                        (<div key={item.link}>
                            <YouTube
                                className={'customYT'}
                                videoId={item.link}
                                opts={opts}>
                            </YouTube>
                            <span
                                style={{
                                    color: '#F5F5F5',
                                    textTransform: 'uppercase'
                                }}
                            >{item.text}</span>
                        </div>)
                    )
                }
            </div>
        )
    }
}

export default VideoGrid;